    // components: {MyPopup},
    data() {

      return {

        month_list: [],
        year_list: [],
        current_year: '',
        current_month: '',
        current_week: 'Aug 13 - Aug 19, 2023',
        pos_profile: '',
        select_date: '',
        allow_date_picker: false,
      };

    },

    methods: {

        update_date() {
            this.allow_date_picker = false
        },
        
        get_year(){
            var me = this;

            frappe.call({
                method: "ts_cloud_gst.ts_cloud_gst.custom.timesheethelper.year_month_finding_and_list",

                callback: function (r) {

                    if(r.message){
                        
                        me.month_list = r.message[1]
                        me.year_list = r.message[0]
                        me.current_year = r.message[2]
                        me.current_month = r.message[3]
                    }

                }
            })
        },

        go_desk() {
            frappe.set_route('/');
            location.reload();
        },
        
        logOut() {
            var me = this;
            me.logged_out = true;

            return frappe.call({
            method: 'logout',
            
            callback: function (r) {
                if (r.exc) {
                return;
                }

                frappe.set_route('/login');
                location.reload();
            },
            });
      },
      
    },

    created: function () {

        // this.get_year()

        this.$nextTick(function () {
            evntBus.$on('show_mesage', (data) => {
            this.show_mesage(data);
            });
            evntBus.$on('set_company', (data) => {
            this.company = data.name;
            this.company_img = data.company_logo
                ? data.company_logo
                : this.company_img;
            });

            
        });
    },
  };
  
